

























import { VueCropper } from 'vue-cropper'
import {Vue, Component, Watch} from "vue-property-decorator";
import myBtn from '@/components/myBtn.vue'
@Component({
    components: {myBtn, VueCropper}
})

export default class cropperCtrl extends Vue {
    option:any = {
        img: '',                         //
        info: true,                      //
        outputSize: 1,                   // 
        outputType: 'jpeg',              //
        canScale: true,                 // 
        autoCrop: true,                  // 
        autoCropWidth: 150,              // 
        autoCropHeight: 150,             // 
        fixed: true,                    //
        fixedNumber: [16, 9]              //
    };
    fileinfo:any = {};
    fileUpload:any = {};
    isShowCropper:boolean = false;

    initCanvas(ev){
        let file = ev.target.files[0];
        let reader = new FileReader();
        reader.readAsDataURL(file);//base64
        let _this = this;
        reader.onload = function(e){
            _this.option.img = e.target.result;
        }
    }

    onCubeImg() {
        let cropper:any = this.$refs.cropper;
        // cropperbase64 
        cropper.getCropData(data => {
            this.fileinfo.url = data
            this.isShowCropper = false

            console.log(data);
            let img:any = document.getElementById(`image`);
            img.src = data;
            //
            // this.option.img = ''

        })
    }


}
